// Components
import BaseHeader from '@/components/argon-core/BaseHeader'
import MainTitle from '@/components/shack/MainTitle.vue'
import { Table, TableColumn } from 'element-ui'

import { mapState } from 'vuex'

export default {
  layout: 'ShackDash',
  components: {
    BaseHeader,
    MainTitle,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  async asyncData({ $eatry, store, error }) {
    await $eatry
      .getAllEatries()
      .then(data => {
        store.commit('eatry/setEatries', data)
      })
      .catch(err => {
        error({
          statusCode: err.status || 404,
          message: err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message
        })
      })
  },
  data() {
    return {
      loading: false
    }
  },
  computed: {
    ...mapState({
      space: state => state.space.currentSpace,
      eatries: state => state.eatry.eatries.data,
      meta: state => state.eatry.eatries.meta,
      links: state => state.eatry.eatries.links
    })
  },
  methods: {
    next() {
      const { next } = this.links

      this.$eatry
        .getAllEatries(next)
        .then(({ data }) => {
          this.$store.commit('eatry/setEatries', data)
        })
        .catch(err => {
          const message = err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message
          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
        })
    },
    prev() {
      const { prev } = this.links

      this.$eatry
        .getAllEatries(prev)
        .then(({ data }) => {
          this.$store.commit('eatry/setEatries', data)
        })
        .catch(err => {
          const message = err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message
          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
        })
    },
    removeEatry(eatryId) {
      this.loading = !this.loading

      this.$eatry
        .deleteOneEatry(eatryId)
        .then(data => {
          this.$store.commit('eatry/removeOneEatry', eatryId)

          this.$bvToast.toast('Eatry successfully deleted', {
            title: 'Success',
            variant: 'success',
            solid: true
          })

          this.loading = !this.loading
        })
        .catch(err => {
          const message = err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message
          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
        })
    }
  }
}
